import { NgModule } from "@angular/core";
import { Routes, RouterModule } from "@angular/router";
import { TasksComponent } from "./tasks/tasks.component";
import { AddTaskComponent } from "../app/add-task/add-task.component";
import { AllTasksComponent } from "../app/all-tasks/all-tasks.component";
import { SearchTaskComponent } from "../app/search-task/search-task.component";
const routes: Routes = [
  { path: "fetchAllPendingTasks", component: TasksComponent },
  { path: "addTask", component: AddTaskComponent },
  { path: "fetchAllTasks", component: AllTasksComponent },
  { path: "searchTask", component: SearchTaskComponent }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule {}
