import { Injectable } from "@angular/core";
import { HttpClient, HttpHeaders } from "@angular/common/http";
import { Observable } from "rxjs";

@Injectable({
  providedIn: "root"
})
export class TaskServiceService {
  constructor(private http: HttpClient) {}

  getTasks(): Observable<Task[]> {
    return this.http.post<Task[]>("http://localhost:9005/fetchAllTasks", 1996);
  }

  searchTasks(content: String): Observable<Task[]> {
    return this.http.post<Task[]>("http://localhost:9005/searchTask", content);
  }

  getPendingTasks(): Observable<Task[]> {
    return this.http.post<Task[]>(
      "http://localhost:9005/fetchAllIncompleteTasks",
      1996
    );
  }

  addTasks(t: Task): Observable<boolean> {
    return this.http.post<boolean>("http://localhost:9005/saveTask", t);
  }

  changeStatusOfTask(id: number): Observable<boolean> {
    return this.http.post<boolean>("http://localhost:9005/changeStatus", id);
  }
}
export interface Task {
  taskId: number;
  userId: number;
  content: String;
  status: String;
}
